
body {
  margin: 0 0 0 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

input {
  width: 20px;
  height: 30px;
  font-size: 20px;
  text-align: center;
}
.body {
  height: 100%;
  text-align: center;
}
.header {
}
.content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 60vh;
}
.freq {
  height: 100%;
  text-align: center;
  width: 50%;
  overflow: scroll;
}
.text {
  height: 100%;
  width: 50%;
}
.textp {
  white-space: pre-wrap;
  text-align: left;
  width: 100%;
  height: 100%;
  overflow: scroll;
}
.box {
  border: solid 3px #000000;
}















































































































.inline {
  display: inline;
}

